.progressContainer {
  width: 50%;
  height: auto;
  display: flex;

  justify-content: center;
  position: relative;
}
.outerCircle {
  width: 160px;
  height: 160px;
  border: 2px solid rgb(219, 212, 212);
  border-radius: 50%;
  padding: 18px;
}

.innerCircle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  border: 2px solid rgb(247, 240, 240);
  display: flex;
  align-items: center;
  justify-content: center;
}

#number {
  font-weight: 600;
  color: purple;
}

.progressContainer > svg {
  position: absolute;
  top: 0;
  left: 26px;
}

circle {
  fill: none;
  stroke: url(#gradientColor);
  stroke-width: 20px;
  stroke-dasharray: 490;
  stroke-dashoffset: 300;
}
