.faqConatainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 5px;
}

.faqTitleIcons {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.faqTitleIcons > p {
  font-size: 3rem;
}
.faqConatainer > ul > li {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.faqAccordion {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.faqAccordion > h2 {
  border-bottom: 2px solid white;
}

.faqAccordion > p {
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
  cursor: pointer;
}

.faqConatainer > ul > li > p {
  text-align: justify;
  word-spacing: 1px;
  font-weight: 400;
  font-family: sans-serif;
  overflow: hidden;
  height: 0;
}
