.skillcontainer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.skilltitleicon {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}

.skilltitleicon > p {
  font-size: 2.5rem;
}

.skillprogressbar {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}
